(function($) {
  $(document).ready(function() {
    // Open overlay
    $(".open-contact-popup").on("click", function() {
      if ($("#popupContactForm").length > 0) {
        $("#popupContactForm").addClass("active");
        $("body").addClass("overflow-hidden");
      } else {
        console.error("No contact form popup found");
      }
    });

    // Close overlay on outside click
    $(".popup-overlay, .popup-overlay-close").on("click", function() {
      $(".popup-overlay").removeClass("active");
      $("body").removeClass("overflow-hidden");
    });

    // Prevent overlay close on overlay content click
    $(".popup-overlay__content").on("click", function(e) {
      e.stopPropagation();
    });

    // Contact form submit
    $("#popupContactForm").on("submit", function(e) {
      e.preventDefault();
      const url = $(this).attr("action");
      const method = $(this).attr("method");
      const serialized = $(this).serialize();

      $.ajax({
        url,
        type: method,
        data: serialized,
        success: function(data) {
          $(".popup-overlay").removeClass("active");
          $("#popupSuccessForm").addClass("active");
        },
        error: function(data) {
          console.error(data);
          $(".popup-overlay").removeClass("active");
          $("body").removeClass("overflow-hidden");
        },
      });
    });

    // Simple phone input mask
    $(".phone-input-mask").on("keypress paste", function(evt) {
      // ^(\([0-9]{3}\)|[0-9]{3}-)[0-9]{3}-[0-9]{4}$
      // console.log($(this).val());
      var theEvent = evt || window.event;

      var key = null;
      // Handle paste
      if (theEvent.type === "paste") {
        key = event.clipboardData.getData("text/plain");
      } else {
        // Handle key press
        key = theEvent.keyCode || theEvent.which;
        key = String.fromCharCode(key);
      }
      var regex = /([1-9()+-])/;
      if (!regex.test(key)) {
        // console.log(1);
        theEvent.returnValue = false;
        if (theEvent.preventDefault) theEvent.preventDefault();
      }
    });
  });
})(jQuery);
